:root {
    --f_1: #F7F4F6;
    --s_2: #52ABB5;
    --t_3: #D89C4F;
    --f_4: #CE5422;
    --f_5: #1C1E32; 

}

div.Card__in > div {
    background-color: #52ABB5;
    color: #fff;
    border-radius: 7px;
    overflow: hidden;
    text-align: center;
    padding: .5em;
}

div.Card__in > div > p {
    font-weight: bold;
    font-size: 1.5em;
    margin: .6em 0 .2em 0;
}

div.Card__in > div > small {
    display: block;
    font-size: .6em;
}

input.input__el {
    background: #F7F4F6;
}

div.FormLayout__row--s-valid {
    color: #52ABB5;
}
div.FormField--s-valid.Input {
    border-color: #52ABB5;
}

div.FormField--s-error.Input {
    border-color: #CE5422;
}
div.FormLayout__row-bottom, div.Placeholder__header {
    color: #CE5422;
}

div.Panel__centered, div.Panel__in, div.Placeholder__in {
    background-color: #1C1E32 !important;
    color: #F7F4F6;
}

div.Placeholder__in {
    padding-bottom: 0;
}

div.div-bottom {
    padding: 30px 0;
    text-align: center;
}

div#start div.Panel__centered {
    background: #1C1E32 url(../img/background.svg) no-repeat center center;
    background-size: cover; 
    color: #F7F4F6;
}

.Spinner {
    color: #CE5422 !important;
}

.txt-bg span {
    display: inline-block;
    background-color: #15172a;
    padding: 3px 6px;
    border-radius: 7px;
}

.txt-bg span:first-child {
    border-radius: 7px 7px 0 0;
}

.txt-bg span:last-child {
    border-radius: 0 0 7px 7px;
}

div.PanelHeader__content, div.PanelHeader__container {
    background: #1C1E32;
    color: #F7F4F6;
}

div.Icon.Icon--back_24 {
    background-color: transparent;
    border: 2px #CE5422 solid;
    border-radius: 100%;
    color: #CE5422;
    width: 24px !important;
    height: 24px !important;
    padding: 0 !important;
    margin-left: 10px;
}

div.Icon.Icon--back_24 svg {
    width: 24px !important;
    height: 24px !important;
}


div.Progress__in {
    background-color: #52ABB5;
}

button.Button.Tappable {
    background-color: #CE5422;
    border: 3px #CE5422 solid;
    color: #F7F4F6;
    border-radius: 30px;
}

button.Button.Tappable .Button__content {
    color: #F7F4F6;
}

.logo {
    display: block;
    width: 100px;
    max-width: 240px;
    height: 100px;
}

.txt-centered {
    text-align: center;
}

.w-100 {
    width: 90%;
}

div.PanelHeader-content > span {
    color: #F7F4F6;
    padding-left: 5px;
}